let labelRenderer = new LabelRenderer(div, { pointerEvents: "none" });
let labels = new Array();
let LabelEnabled = false;
let labelNode
let labelPos

function mouseOver(node, index, position){
  if (node.depth > currentDepth) return;
  LabelEnabled = true;
  labelNode = node;
  labelPos = graph.getNodePositionsMap().get(node.id);
  console.log(labelNode)
  updateLabelPosition();
}
function mouseOut(node, index, position){
  if (node.depth > currentDepth) return;
  LabelEnabled = false;
  updateLabelPosition();
}



function calculateLabelPosition(node) {
  let spacePos = labelPos;
  let screenPosition = graph.spaceToScreenPosition(spacePos);
  let radius = graph.spaceToScreenRadius(
      Number(graph.getNodeRadiusById(node.id))
  );
  return [screenPosition[0], screenPosition[1] - (radius + 2),]
}


function updateLabelPosition(){
  if (!LabelEnabled){
    labels.pop()
  }
  else{
    let screenPosition = calculateLabelPosition(labelNode)
    labels[0] = {
        id: labelNode.id,
        text: labelNode.name,
        x: screenPosition[0],
        y: screenPosition[1],
        opacity: 1
    };
  
  }
  

  labelRenderer.setLabels(labels);
  labelRenderer.draw(true);
}

