function printPositions(){
  console.log(Array.from(graph.getNodePositionsMap()));
}

function loadPositions(){
  let map = new Map;
  mapArray.forEach(entry => {
                    map[entry[0]] = entry[1];
                  })

  let graphNodes = nodes
  console.log("loading positions")
  graphNodes.forEach((node)=> {
    let nodePosition = map[node.id];
    if(nodePosition){
      console.log("test working")
      node.x = nodePosition[0];
      node.y = nodePosition[1];
      };
    }
  )
  graph.setData(graphNodes, links);
  if (lowSpecMode) graph.pause();


  
}

function setDepthPhysics(depth){
  let graphNodes = nodes.filter((n) => n.depth <= depth)
  let graphLinks = links.filter((n) => n.depth <= depth)
  currentDepth = depth
  //carry existing positions over
  const nodePositionsMap = graph.getNodePositionsMap();

  graphNodes.forEach((node)=> {
      let nodePosition = nodePositionsMap.get(node.id)
      if(nodePosition){
        node.x = nodePosition[0];
        node.y = nodePosition[1];
      }
    }
  )
  graph.setData(graphNodes, graphLinks)
}


function setDepthVisual(depth){
  currentDepth = depth
  //carry existing positions over
  var graphConfig = config
  console.log("depth:" + depth)
  graphConfig.nodeColor = ((node) => {
    if (node.depth > depth){
      return "rgba(100,100,100,00)";
    }
    else{
      return nodeColorFunc(node)
    }
  })
  graphConfig.linkColor = ((link) => {
    if (link.depth > depth){
      return "rgba(0,0,0,0)";
    }
    else{
      return linkColorFunc(link);
    }
  })
  console.log(graphConfig)
  graph.setConfig(graphConfig);

}


function setDepth(depth){
  //needs a self link on node with depth 0
  if (depth > total_depth){return}
  console.log("0")

  if (!lowSpecMode){
    setDepthPhysics(depth);
  }else{
    setDepthVisual(depth);
  }
  current_depth_var.innerHTML = currentDepth

  //current_separate_dependencies_var.innerHTML   = dependencies_by_depth_separate[currentDepth]
  //contributors_year_accumulated_var.innerHTML   = contributors_year_accumulated[currentDepth]
  //current_aggregate_dependencies_var.innerHTML  = dependencies_by_depth_aggregated[currentDepth]
  //companies_total_var.innerHTML                 = comany_count_by_depth_aggregated[currentDepth]  
  commits_year_accumulated_var.innerHTML        = Math.floor(commits_year_accumulated[currentDepth]/365)


  let countUp = new CountUp('current_separate_dependencies_var', dependencies_by_depth_separate[currentDepth], {startVal:dependencies_by_depth_separate[currentDepth-1]});
  countUp.start();

  countUp = new CountUp('current_aggregate_dependencies_var', dependencies_by_depth_aggregated[currentDepth], {startVal:dependencies_by_depth_aggregated[currentDepth-1]});
  countUp.start();

  countUp = new CountUp('contributors_year_accumulated', contributors_year_accumulated[currentDepth], {startVal:contributors_year_accumulated[currentDepth-1]});
  countUp.start();



  countUp = new CountUp('companies_total', comany_count_by_depth_aggregated[currentDepth], {startVal:comany_count_by_depth_aggregated[currentDepth-1]});
  countUp.start();

  countUp = new CountUp('commits_year_accumulated', Math.floor(commits_year_accumulated[currentDepth]/365), {startVal:Math.floor(commits_year_accumulated[currentDepth-1]/365)});
  countUp.start();


}


  
function nodeColorFunc2(node){
  let num = node.depth;
  let colorScale = d3.scaleLinear()
  .domain([-0.7, 1.5, 7])
  .range(["orangered", "orange", "white"])

  return colorScale(num)
  if (num<1){
    return "rgb(0, 100, 0)"
  }
  if (num<2){
    return "rgb(24, 118, 0)"
  }
  if (num<3){
    return "rgb(123, 177, 0)"
  }
  if (num<4){
    return "rgb(211, 229, 0)"
  }
  if (num<5){
    return "rgb(254, 234, 0)"
  }
  if (num<6){
    return "rgb(254, 171, 0)"
  }
  if (num<7){
    return "rgb(254, 122, 0)"
  }
  else{

    return "rgb(254, 57, 0)"
  }
}

function nodeColorFunc1(node){
  let num =node.direct_dependencies;

  if (num<1){
    return "rgb(0, 100, 0)"
  }
  if (num<10){
    return "rgb(24, 118, 0)"
  }
  if (num<25){
    return "rgb(123, 177, 0)"
  }
  if (num<50){
    return "rgb(211, 229, 0)"
  }
  if (num<100){
    return "rgb(254, 234, 0)"
  }
  if (num<200){
    return "rgb(254, 171, 0)"
  }
  if (num<500){
    return "rgb(254, 122, 0)"
  }
  else{

    return "rgb(254, 57, 0)"
  }
}

function linkColorFunc(link){
  return "rgba(" + 200 + ", " + 200 + ", " + 200 +" ,0.4)"
}

function updateChoropleth(){
  d3.selectAll("svg > *").remove();
  d3.queue()
  .defer(d3.json, "https://raw.githubusercontent.com/holtzy/D3-graph-gallery/master/DATA/world.geojson")
  .defer(d3.csv, "https://raw.githubusercontent.com/holtzy/D3-graph-gallery/master/DATA/world_population.csv", function(d) { choropleth_data.set(d.code, +0); })
  .await(ready);
}


function loadDepth(depth){
  let current_distrib = country_distribution_by_depth_aggregated[depth]
  let keys = Object.keys(current_distrib)
  keys.forEach((key)=>{ 
    choropleth_data.set(getCountryISO3(key.toUpperCase()), current_distrib[key])
  })
}


